@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Quicksand', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.sticky{position:-webkit-sticky;position:sticky;top:0}

.app{position:relative;display:grid;grid-template-columns:1fr 2fr;grid-template-rows:50px 1fr;grid-template-areas:"header header" "contact-list contact"}.app header{grid-area:header;display:grid;grid-template-columns:1fr 9fr;grid-template-areas:"logo search ";padding:10px;background-color:white;box-shadow:0px 5px 10px rgba(0,0,0,0.1)}.app header .logo{grid-area:logo;place-self:center start}.app header .search{grid-area:search;place-self:center end}.app .contact-list{display:grid;grid-template-rows:50px auto;grid-template-columns:1fr;height:100vh;overflow-y:auto}.app .contact-list .contact-list--title{display:grid;grid-template-columns:60px repeat(2, 1fr);place-items:center start;background-color:white;border-bottom:1px solid black}.app .contact-list .contact-list--item{display:grid;grid-template-columns:auto repeat(2, 1fr);border-bottom:1px solid gray;place-items:center start}.app .contact-list .contact-list--item:hover{background-color:rgba(0,0,0,0.08);cursor:pointer}.app .contact-list .contact-list--item img{width:50px;border-radius:50%;margin:5px;-webkit-transition:0.5s;transition:0.5s}.app .contact-list .contact-list--item img:hover{box-shadow:0px 0px 20px rgba(0,0,0,0.4)}.app .contact{grid-area:contact;display:grid;grid-template-rows:auto 1fr;grid-template-columns:1fr;overflow-x:hidden;overflow-y:auto}.app .contact .contact--profile-image{width:100%;grid-row:1 / span 1;grid-column:1 / span 1;height:400px;object-fit:cover;background-repeat:no-repeat;background-size:100%;background-position:center;-webkit-filter:blur(20px) brightness(0.8) grayscale(0.2);filter:blur(20px) brightness(0.8) grayscale(0.2)}.app .contact img{grid-row:1 / span 1;grid-column:1 / span 1;z-index:1;height:200px;place-self:center;border-radius:20px;box-shadow:0 0 20px darkgray;object-fit:cover;-webkit-transition:.1s ease-in;transition:.1s ease-in}.app .contact img:hover{height:300px}.app .contact .contact--info{padding:100px;font-size:20px}.app .contact .contact--info div{margin-bottom:20px}.app .contact .contact--info strong{font-size:20px}

